.burger {
    display: inline-block;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.burger:hover > .line {
    background-color: var(--secondary-clr);
}

.burger .line {
    width: 25px;
    background-color: var(--primary-clr-text);
    padding: 1.2px;
    border-radius: var(--size-md-e-1);
    transition: transform 200ms ease-out, background-color 50ms ease-in;
    will-change: transform, background-color;
}

.burger .line + .line {
    margin-top: 3px;
}

.burger.active {

    // &:hover > .line {
    //     background-color: var(--secondary-clr);
    // }

    .line {
        margin-top: 0;
        background-color: var(--secondary-clr);
    }
}

.burger.active .line1 {
    // transform: rotate(-135deg) translate(-2px, -2px);
    transform: rotate(-135deg) translate(0px, 1px);
}

.burger.active .line2 {
    transform-origin: center;
    transform: scaleX(0);
}

.burger.active .line3 {
    // transform: rotate(135deg) translate(-2px, 2px);
    transform: rotate(135deg) translate(-4px, 4px);
}