@media all and (min-width: 768px) {
    .main-header {
        .medium-type,
        .large-type {
            display: initial;
        }
    }

    .page .side {
        // position: initial;
        width: var(--side-nav-default-width); 
        //min-width: calc( var(--size-md-e-2) * 4);
        //max-width: calc( var(--size-md-e-1) * 10);
        transform: scaleX(1);
    }

    .page .side .side-nav .side-nav-list .item-container .item{
        display: flex;
        flex-direction: column;
        align-items: center;

        svg {
            font-size: var(--size-lg-e-1);
        }

        span {
            font-size: var(--size-sm-e-2);
        }

    }

    .page .side.active {
        position: fixed;
        top: var(--main-header-height);
        left: 0;
        width: calc(var(--side-nav-default-width) * 10 - 30%); // 50%
    }

    .page .main-content {
        margin-left: var(--side-nav-default-width);
    }
}
