/* E */
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* vars */
:root {
    /*clrs*/
    // type-name-brightness
    /*w*/
    --clr-white-255: #fff;
    --clr-white-190: rgb(192, 192, 192);
    --clr-white-230: rgb(230, 230, 230);
    --clr-white-245: #fafbfc;
    --clr-white-220: #ddd;
    /*w*/
    --clr-black-0: #000;
    --clr-black-10: rgba(56, 56, 56, 0.911);
    /*r*/
    --clr-red-0: #f00;
    /*g*/
    --clr-green-255: #0f0;
    /*p*/
    --clr-purple-200: rgb(145, 0, 189);
    --clr-purple-220: rgba(145, 0, 189, 0.555);
    /*b*/
    --clr-blue-8: rgb(22, 30, 41);
    --clr-blue-9: rgba(27, 35, 46, 0.8);
    --clr-blue-10: rgb(32, 41, 54);
    --clr-blue-11: rgb(37, 47, 61);
    --clr-blue-40: rgb(48, 59, 75);
    --clr-blue-50: rgb(69, 81, 97);

    // name-type-number
    /*primary's*/
    --primary-clr: var(--clr-blue-10);
    --primary-clr-2: var(--clr-blue-8);
    --primary-clr-3: var(--clr-blue-9);
    --primary-clr-text: var(--clr-white-255);
    /*secondary's*/
    --secondary-clr: var(--clr-purple-200);
    --secondary-clr-text: var(--clr-white-190);

    /*f-s*/
    // name-scale-unit-number
    --size-sm-e-1: 0.25em;
    --size-sm-e-2: 0.5em;
    --size-sm-e-3: 0.75em;
    --size-md-e-1: 1em;
    --size-md-e-2: 1.25em;
    --size-md-e-3: 1.5em;
    --size-lg-e-1: 2em;
    --size-lg-e-2: 3em;
    --size-lg-e-3: 4em;

    --size-sm-r-1: 0.25rem;
    --size-sm-r-2: 0.5rem;
    --size-sm-r-3: 0.75rem;
    --size-md-r-1: 1rem;
    --size-md-r-2: 1.25rem;
    --size-md-r-3: 1.5rem;
    --size-lg-r-1: 2rem;
    --size-lg-r-2: 3rem;
    --size-lg-r-3: 4rem;

    /*percent*/
    --size-xsm-per-1: 0%;
    --size-xsm-per-2: 1%;
    --size-xsm-per-3: 8%;
    --size-sm-per-1: 10%;
    --size-sm-per-2: 20%;
    --size-sm-per-3: 25%;
    --size-md-per-1: 40%;
    --size-md-per-2: 50%;
    --size-md-per-3: 60%;
    --size-lg-per-1: 75%;
    --size-lg-per-2: 90%;
    --size-lg-per-3: 100%;

    --main-header-height: var(--size-xsm-per-3);
    --main-page-height: calc(100% - var(--main-header-height));

    --side-nav-default-width: var(--size-xsm-per-3);
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    background-color: var(--primary-clr);
}

/* body */
// default/mobile "breakpoint"
.App {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: var(--primary-clr);
    color: var(--primary-clr-text);

    /*scrollbar*/
    ::-webkit-scrollbar {
        width: var(--size-sm-e-3);
    }

    ::-webkit-scrollbar-track {
        //width: var(--size-sm-e-3);
        background-color: var(--primary-clr-2);
    }

    ::-webkit-scrollbar-thumb {
        //width: var(--size-sm-e-3);
        background-color: var(--primary-clr);
        transition: all 500ms ease;
        border: none;
    }

    ::-webkit-scrollbar-thumb:hover {
        border: 1px solid var(--primary-clr-3);
    }

    &.App--light {
        /*primary's*/
        --primary-clr: var(--clr-white-255);
        --primary-clr-2: var(--clr-white-220);
        --primary-clr-3: var(--clr-white-190);
        --primary-clr-text: var(--clr-black-0);
        /*secondary's*/
        --secondary-clr: var(--clr-purple-200);
        --secondary-clr-text: var(--clr-black-10);
    }
}

@import "./_KeyFrames";

/* defaults */
a {
    text-decoration: none;
    color: var(--primary-clr-text);
}

.btn-type-1 {
    position: relative;
    padding: var(--size-sm-e-1);
}

.btn-type-1::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: 100%;
    background-color: currentColor;
    transform-origin: right;
    transform: scaleX(0);
    transition: transform 150ms ease-in;
    will-change: transform;
}

.btn-type-1:hover::after {
    transform-origin: left;
    transform: scaleX(1);
}

.btn-type-2 {
    position: relative;
    padding: var(--size-sm-e-1);
    transition: background-color 100ms ease;
}

.btn-type-2:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

.btn-type-3 {
    background-color: var(--primary-clr);
    font-size: var(--size-md-e-1);
    color: var(--primary-clr-text);
    border: 1px solid var(--primary-clr-text);
    padding: var(--size-sm-r-1);
    cursor: pointer;
    transition: background-color 100ms ease;
}

.btn-type-3:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

/* content */
.main-header {
    padding: var(--size-md-e-1) 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    min-height: var(--main-header-height);
    height: var(--main-header-height);
    background-color: var(--primary-clr-2);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    border-bottom: 2px solid var(--primary-clr-3);

    svg {
        font-size: var(--size-md-e-3);
    }

    .btn-type-1 {
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .app-name {
        font-size: var(--size-md-e-3);
    }

    .medium-type,
    .large-type {
        display: none;
    }
}

@import "./accessories/_Burger";

.main-nav {
    padding: 0 var(--size-lg-e-1);
    min-width: 100%;
}

.main-nav-list {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.page {
    position: absolute;
    top: var(--main-header-height);
    display: flex;
    width: 100%;
    height: var(--main-page-height); // calc(100% - var(--main-header-height));
    min-height: var(--main-page-height); // calc(100% - var(--main-header-height));
    color: var(--primary-clr-text);
    background-color: var(--primary-clr);
}

.page .side {
    position: fixed;
    top: var(--main-header-height);
    left: 0;
    bottom: 0;
    //min-height: 100%;
    max-height: 100%;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: #bcd transparent;
    flex-shrink: 0;
    flex-grow: 0;
    transform-origin: left;
    width: 0%;
    transform: scaleX(0);
    background-color: var(--primary-clr-2);
    transition: width 200ms ease, transform 100ms ease-in-out,
        position 1000ms ease;
    will-change: width, transform, position, display;
    border-top: 1px solid var(--primary-clr-3);
    border-right: 1px solid var(--primary-clr-3);
    //color: var(--secondary-clr-text);

    a {
        color: var(--secondary-clr-text);
    }
}

.page .side .side-nav {
    min-height: 100%;
    height: 100%;
}

.page .side .side-nav .side-nav-list {
    min-height: 100%;
    height: 100%;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    //justify-content: space-around;
}

.page .side .side-nav .side-nav-list .item-container {
    display: block;
    width: 100%;

    .item {
        display: block;
        width: 100%;
        padding: var(--size-md-e-1);
        will-change: flex-direction, display, padding;
    }
}

.page .side.active {
    //position: absolute;
    left: 0;
    width: calc(var(--side-nav-default-width) * 10 - 10%); // 70%
    transform: scaleX(1);
    z-index: 10;
    //transition: width 300ms ease;
}

.page .side.active .side-nav .side-nav-list .item-container .item {
    flex-direction: row;

    svg {
        margin-left: var(--size-md-e-1);
        font-size: var(--size-md-e-2);
    }

    span {
        margin-left: var(--size-md-e-1);
        font-size: var(--size-md-e-2);
    }
}

.page .side.active + .main-content {
    overflow: hidden;
    z-index: 1;
}

.page .side.active + .main-content::after {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 3;
}

.page .main-content {
    position: relative;
    flex-grow: 1;
    padding: var(--size-lg-e-1);
    height: 100%;
    min-height: 100%;
    max-height: 100%;
    overflow-y: auto;
    will-change: width;
    color: var(--primary-clr-text);

    .loading-svg {
        display: block;
        position: absolute;
        // top: 30%;
        // left: calc(50% - var(--side-nav-default-width));
        font-size: var(--size-lg-e-3);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto auto;
        animation: spin 3000ms infinite linear;
        will-change: transform;
    }

    header:first-of-type {
        display: flex;
        justify-content: space-between;
        margin-bottom: var(--size-lg-r-1);
        padding-bottom: var(--size-sm-e-3);
        border-bottom: 1px solid var(--primary-clr-3);
    }
}

