@media all and (min-width: 1024px) {
    .page .side {
        position: initial;
    }

    .page .side.active {
        position: initial;
        width: calc(var(--side-nav-default-width) * 3 - 6%); // 18%
        //min-width: calc( var(--size-md-e-2) * 10);
        //max-width: calc( var(--size-md-e-1) * 17 );
    }

    .page .side.active + .main-content {
        overflow-y: auto;
    }

    .page .side.active + .main-content::after {
        display: none;
    }

    .page .main-content {
        margin-left: 0;
    }
}
