// slider on settings page
.slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 4%;
    bottom: 10%;
    background-color: var(--primary-clr-text);
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border-radius: 50%;
}

.switch input:checked + .slider:before {
    -webkit-transform: translateX(calc(100% + 4px));
    -ms-transform: translateX(calc(100% + 4px));
    transform: translateX(calc(100% + 4px));
    background-color: var(--primary-clr-text);
}

.change-theme-container > .change-theme-wrapper{
    position: relative;
    margin-left: var(--size-lg-r-1);

    .switch {
        margin: 0 var(--size-sm-e-3) 0;
    }
}